body {
  
  padding: 30px;
  margin: 0;
  font-family: 'Arial' sans-serif;
}
.navbar-dark .navbar-brand{
  font-size:36px !important;
  font-weight: bold !important;
}
.navbar-custom{
  background: #79c7e8 !important;
  margin-bottom:15px;
}

.margin-20{

  margin-bottom:25px;
}
.sub-header{
  padding-bottom:5px;
  padding-left:5px;
  
  border-left:5px solid #79c7e8;
}

.line-hr{
  border-bottom:3px solid #79c7e8;
  margin-top:0px;
}
p{
  font-size: 16px;
  letter-spacing: .2px;
  display: block;
}
span.arrow{
  padding:2px 6px;
  color: #fff;
  border-radius: 3px;
  background:#79c7e8;
}
p a{
  text-decoration: underline;
}
.nav-link{
  font-size:16px;
  color: #fff !important;
  text-transform: uppercase;
}

.footer{
  padding:20px;
  background: #79c7e8 !important;
  color: #fff;
  font-weight: bolder;
  font-size:14px;
}
h1, h2, p, ul, li {
  
}
ul.header li {
  display: inline;
  list-style-type: none;
  margin: 0;
}
ul.header {
  background-color: #111;
  padding: 0;
}
ul.header li a {
  color: #FFF;
  font-weight: bold;
  text-decoration: none;
  padding: 20px;
  display: inline-block;
}

.content {
  background-color: #FFF;
  padding: 20px;
}
.content h2 {
  padding: 0;
  margin: 0;
}
.content li {
  margin-bottom: 10px;
}